<template>
    <div class="padding_limit">
        <!--  -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ path: '/tasklaunch' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <div>
            <el-dialog width="600px" title="" :visible.sync="addTaskDialogVisible">
                <el-form
                    ref="addTaskFormRef"
                    :rules="taskFormRuls"
                    label-width="100px"
                    :model="addTaskForm"
                >
                    <!--  -->
                    <el-form-item prop="name" label="">
                        <el-input v-model="addTaskForm.name"></el-input>
                    </el-form-item>
                    <!--  -->
                    <el-form-item prop="time" label="">
                        <el-row>
                            <el-col>
                                <el-date-picker
                                    type="daterange"
                                    unlink-panels
                                    start-placeholder=""
                                    end-placeholder=""
                                    placeholder="/"
                                    format="yyyy  MM  dd "
                                    value-format="yyyy-MM-dd"
                                    v-model="addTaskForm.time"
                                ></el-date-picker>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <!--  -->
                    <el-form-item prop="format" label="">
                        <el-select
                            v-model="addTaskForm.format"
                            placeholder=""
                            style="width: 85%;"
                        >
                            <el-option
                                v-for="item in options"
                                :key="item.label"
                                :label="item.label"
                                v-model="item.value"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <!--  -->
                    <el-form-item class="btns">
                        <el-button @click="handleAddTaskSubmit" type="primary"></el-button>
                        <el-button @click="changeAddTaskDialog" type="info"></el-button>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>

        <!--  -->
        <div>
            <el-dialog width="600px" title="" :visible.sync="editTaskDialogVisible">
                <el-form
                    ref="editFormRef"
                    :rules="taskFormRuls"
                    label-width="100px"
                    v-model="editTaskForm"
                >
                    <el-form-item label="">
                        <el-input disabled v-model="editTaskForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-row>
                            <el-col>
                                <el-date-picker
                                    type="daterange"
                                    unlink-panels
                                    start-placeholder=""
                                    end-placeholder=""
                                    placeholder="/"
                                    format="yyyy  MM  dd "
                                    value-format="yyyy-MM-dd"
                                    v-model="editTaskForm.time"
                                ></el-date-picker>
                            </el-col>
                        </el-row>
                    </el-form-item>
                    <!--  -->
                    <el-form-item label="">
                        <el-select disabled v-model="editTaskForm.format.label" style="width: 85%;"></el-select>
                    </el-form-item>
                    <!--  -->
                    <el-form-item class="btns">
                        <el-button @click="handleEditTaskSubmit()" type="primary"></el-button>
                        <el-button
                            @click="editTaskDialogVisible = !editTaskDialogVisible;"
                            type="info"
                        ></el-button>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>

        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="13">
                    <el-input placeholder="">
                        <el-button
                            @click="$message.info('')"
                            slot="append"
                            icon="el-icon-search"
                        ></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button @click="changeAddTaskDialog" type="primary"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table
                :data="tasklist"
                border
                stripe
                @expand-change="expandSelect"
                :row-key="getRowKeys"
                :expand-row-keys="expandList"
            >
                <!--  -->
                <el-table-column type="expand" align="center" label="" :resizable="false">
                    <template v-slot="scope1">
                        <div class="infoTable_box">
                            <p class="expandTitle"></p>
                            <el-table :data="unDoPeop" width="500px">
                                <el-table-column align="center" type="index" label></el-table-column>
                                <el-table-column align="center" prop="studentNumber" label="/"></el-table-column>
                                <el-table-column align="center" prop="name" label=""></el-table-column>
                            </el-table>
                            <p class="expandTitle" style="margin-top: 12px"></p>
                            <el-table :data="DonePeop" width="500px">
                                <el-table-column align="center" type="index" label></el-table-column>
                                <el-table-column align="center" prop="studentNumber" label="/"></el-table-column>
                                <el-table-column align="center" prop="name" label=""></el-table-column>
                                <el-table-column align="center" label="">
                                    <template v-slot="scope2">
                                        <el-button
                                            @click="downloadById(scope1.row.taskId, scope2.row.userId)"
                                            type="warning"
                                            icon="el-icon-download"
                                        ></el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column align="center" type="index" label="" :resizable="false"></el-table-column>
                <!--  -->
                <el-table-column align="center" prop="taskName" label="" :resizable="false"></el-table-column>
                <!--  -->
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot:default="scope">
                        <i class="el-icon-time"></i>
                        <span
                            style="margin-left: 10px;"
                        >{{dataToNormalFormat(scope.row.taskBeginTime)}}</span>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot:default="scope">
                        <i class="el-icon-time"></i>
                        <span
                            style="margin-left: 10px;"
                        >{{dataToNormalFormat(scope.row.taskEndTime)}}</span>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot:default="scope">
                        <i
                            v-if="scope.row.undoIds.length == 0"
                            class="el-icon-circle-check"
                            style="color: green; font-size: 15px;"
                        ></i>
                        <span
                            v-else
                        >{{scope.row.doneIds.length}} / {{scope.row.undoIds.length + scope.row.doneIds.length}}</span>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column align="center" label=" (||)" :resizable="false">
                    <template v-slot:default="scope">
                        <div>
                            <el-button
                                @click="changeEditTaskDialog(scope)"
                                type="primary"
                                icon="el-icon-edit"
                            ></el-button>
                            <el-button
                                @click="removeById(scope.row.taskId)"
                                type="danger"
                                icon="el-icon-delete"
                            ></el-button>
                            <el-button
                                @click="downloadAll(scope.row.taskId)"
                                type="warning"
                                icon="el-icon-download"
                            ></el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>

<script>
import fileDownload from "js-file-download";
export default {
    data() {
        return {
            // id
            expandList: [],
            // id
            getRowKeys(row) {
                return row.taskId;
            },
            // scope.row.id
            unDoPeop: [
                // {
                //     userId: "1L",
                //     studentNumber: "1812001000",
                //     name: "",
                //     classroom: "1",
                //     institude: "Software",
                //     major: "C.S.",
                //     grade: "2018",
                //     school: "JIT",
                // },
            ],
            // scope.row.id
            DonePeop: [
                // {
                //     userId: "1L",
                //     studentNumber: "1812001000",
                //     name: "",
                //     classroom: "1",
                //     institude: "Software",
                //     major: "C.S.",
                //     grade: "2018",
                //     school: "JIT",
                // },
            ],
            // 
            tasklist: [
                // {
                //     taskId: 1,
                //     luncherId: 2,
                //     taskBeginTime: "2020_09_06_20_20",
                //     taskEndTime: "2020_09_13_20_20",
                //     undoIds: [1, 4],
                //     doneIds: [2, 3, 5, 6, 7, 8],
                //     taskName: "",
                //     taskFileDirectory: "",
                //     taskFileTemplate: "/home/file/template/qwer.txt",
                //     taskFileSample: "/home/file/sample/asdf.txt",
                //     format: "&*#",
                // },
                // {
                //     taskId: 2,
                //     luncherId: 2,
                //     taskBeginTime: "2020_09_06_20_20",
                //     taskEndTime: "2020_09_12_20_20",
                //     undoIds: [],
                //     doneIds: [1, 2, 3, 4, 5, 6, 7, 8],
                //     taskName: "",
                //     taskFileDirectory: "",
                //     taskFileTemplate: "/home/file/template/qwer.txt",
                //     taskFileSample: "/home/file/sample/asdf.txt",
                //     format: "*&#",
                // },
            ],
            // 
            queryInfo: {
                query: "",
                pagenum: 1,
                pagesize: 10,
            },
            // 
            addTaskDialogVisible: false,
            // 
            editTaskDialogVisible: false,
            // 
            addTaskForm: {
                name: "",
                time: [],
                format: "",
            },
            // 
            editTaskForm: {
                taskId: "",
                name: "",
                time: [],
                format: {
                    label: "",
                    value: "",
                },
            },
            // 
            options: [
                {
                    label: "----",
                    value: "#*&",
                },
                {
                    label: "----",
                    value: "#&*",
                },
                {
                    label: "----",
                    value: "*&#",
                },
                {
                    label: "----",
                    value: "&*#",
                },
                {
                    label: "--",
                    value: "&*",
                },
                {
                    label: "--",
                    value: "*&",
                },
                {
                    label: "",
                    value: "&",
                },
                {
                    label: "",
                    value: "*",
                },
            ],
            // 
            taskFormRuls: {
                name: [
                    { required: true, message: "", trigger: "blur" },
                ],
                time: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                format: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    async created() {
        this.getTasksList();
    },
    methods: {
        // ,
        async expandSelect(row, expandedRows) {
            let flag = false;
            var that = this;
            if (expandedRows.length) {
                that.expandList = [];
                if (row) {
                    that.expandList.push(row.taskId);
                }
            } else {
                that.expandList = [];
            }
            let tmpunDoPeop = [];
            let tmpDonePeop = [];
            for (let i = 0; i < row.undoIds.length; ++i) {
                let params = new URLSearchParams();
                params.append("userId", row.undoIds[i]);
                let user = await this.$http.post("/user/toStudent", params);
                console.log(user);
                if (user.data.code != 200) flag = true;
                else tmpunDoPeop.push(user.data.data);
            }
            for (let i = 0; i < row.doneIds.length; ++i) {
                let params = new URLSearchParams();
                params.append("userId", row.doneIds[i]);
                const user = await this.$http.post("/user/toStudent", params);
                if (user.data.code != 200) flag = true;
                else tmpDonePeop.push(user.data.data);
            }
            if (flag) this.$message.error("");
            this.unDoPeop = tmpunDoPeop;
            this.DonePeop = tmpDonePeop;
        },
        // 
        async getTasksList() {
            this.tasklist = [];
            let flag = false;
            const res = await this.$http.get("/user/userTasks");
            // console.log(res);
            if (res.data.code != 200)
                return this.$message.error("");
            //  
            for (let i = 0; i < res.data.data.lunchedTasks.length; ++i) {
                let params = new URLSearchParams();
                params.append("taskId", res.data.data.lunchedTasks[i]);
                // console.log(res.data.data.lunchedTasks[i]);
                const task = await this.$http.post(
                    "/user/taskIdToTask",
                    params
                );
                // console.log(task);
                if (task.data.code != 200) flag = true;
                else this.tasklist.push(task.data.data);
                if (!this.tasklist[i].doneIds) this.tasklist[i].doneIds = [];
                if (!this.tasklist[i].undoIds) this.tasklist[i].undoIds = [];
                // console.log(this.tasklist[i])
            }
            if (flag) this.$message.error("");
        },
        // id
        removeById(id) {
            // 
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(async (res1) => {
                    // console.log(res);
                    let params = new URLSearchParams();
                    params.append("taskId", id);
                    const res2 = await this.$http.post("/task/delete", params);
                    if (res2.data.code != 200)
                        return this.$message.error("");
                    this.$message.success("");
                    this.getTasksList();
                })
                .catch((error) => {});
        },
        // 
        changeAddTaskDialog() {
            this.addTaskDialogVisible = !this.addTaskDialogVisible;
        },
        // 
        changeEditTaskDialog(scope) {
            console.log(scope);
            this.editTaskForm.taskId = scope.row.taskId;
            this.editTaskForm.name = scope.row.taskName;
            let tmptime = [];
            tmptime[0] = this.dataToNormalFormat(scope.row.taskBeginTime);
            tmptime[1] = this.dataToNormalFormat(scope.row.taskEndTime);
            this.editTaskForm.time = tmptime;
            let tmp = {
                "*": "",
                "&": "",
                "#": "",
            };
            let strtmp = tmp[scope.row.format[0]];
            for (let i = 1; i < scope.row.format.length; ++i) {
                strtmp = strtmp + "--" + tmp[scope.row.format[i]];
            }
            // console.log(strtmp);
            this.editTaskForm.format.label = strtmp;
            this.editTaskForm.format.value = scope.row.format;
            // console.log(this.editTaskForm.format)
            this.editTaskDialogVisible = !this.editTaskDialogVisible;
        },
        // 
        handleAddTaskSubmit() {
            this.$refs.addTaskFormRef.validate(async (valid) => {
                // console.log(this.addTaskForm.format);
                // console.log(valid);
                if (!valid) {
                    return this.$message.error("");
                }
                let params = new URLSearchParams();
                params.append("taskName", this.addTaskForm.name);
                params.append("format", this.addTaskForm.format);
                params.append(
                    "taskBeginTime",
                    this.dataToUploadFormat(this.addTaskForm.time[0])
                );
                params.append(
                    "taskEndTime",
                    this.dataToUploadFormat(this.addTaskForm.time[1])
                );
                const res = await this.$http.post("/task/launch", params);
                // console.log(res);
                if (res.data.code != 200)
                    return this.$message.error("");
                this.$message.success("");
                this.changeAddTaskDialog();
                this.getTasksList();
                this.$refs.addTaskFormRef.resetFields();
            });
        },
        // 
        async handleEditTaskSubmit() {
            // console.log(this.editTaskForm.taskId);
            let params = new URLSearchParams();
            params.append("taskId", this.editTaskForm.taskId);
            params.append(
                "taskBeginTime",
                this.dataToUploadFormat(this.editTaskForm.time[0])
            );
            params.append(
                "taskEndTime",
                this.dataToUploadFormat(this.editTaskForm.time[1])
            );
            const res = await this.$http.post("/task/update", params);
            if (res.data.code != 200) return this.$message.error("");
            this.$message.success("");
            location.reload();
        },
        // 
        dataToNormalFormat(data) {
            return (
                data.substr(0, 4) +
                "-" +
                data.substr(5, 2) +
                "-" +
                data.substr(8, 2)
            );
        },
        // 
        dataToUploadFormat(data) {
            return (
                data.substr(0, 4) +
                "_" +
                data.substr(5, 2) +
                "_" +
                data.substr(8, 2) +
                "_00_00"
            );
        },
        // 
        async downloadAll(taskId) {
            let params = new URLSearchParams();
            params.append("taskId", taskId);
            const res = await this.$http.post("/file/package/download", params);
            const uuid = res.data.data;
            let a = document.createElement("a");
            a.href = `${this.$store.state.downloadURL}` + uuid;
            console.log(a.href);
            document.body.appendChild(a);
            a.click(); //
            URL.revokeObjectURL(a.href); // URL 
            document.body.removeChild(a); //  a 
        },
        // 
        async downloadById(taskId, userId) {
            console.log(taskId);
            console.log(userId);
            let params = new URLSearchParams();
            params.append("taskId", taskId);
            params.append("userId", userId);
            const res = await this.$http.post("/file/download", params);
            console.log(res);
            const uuid = res.data.data;
            let a = document.createElement("a");
            a.href = `${this.$store.state.downloadURL}` + uuid;
            console.log(a.href);
            document.body.appendChild(a);
            a.click(); //
            URL.revokeObjectURL(a.href); // URL 
            document.body.removeChild(a); //  a 
        },
        
    },
};
</script>

<style lang="less" scoped>
.el-table th.gutter {
    display: table-cell !important;
}
.infoTable_box {
    margin: auto;
    width: 650px;
    text-align: center;
}
.line {
    text-align: center;
}
.btns {
    display: flex;
    justify-content: flex-end;
}
.expandTitle {
    letter-spacing: 3px;
}
</style>