<template>
    <div class="padding_limit">
        <!--  -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ path: '/taskfinished' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="13">
                    <el-input placeholder="">
                        <el-button
                            @click="$message.info('')"
                            slot="append"
                            icon="el-icon-search"
                        ></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="tasklist" border stripe>
                <!--  -->
                <el-table-column align="center" type="index" label="" :resizable="false"></el-table-column>
                <!--  -->
                <el-table-column align="center" prop="taskName" label="" :resizable="false"></el-table-column>
                <!--  -->
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot:default="scope">
                        <i class="el-icon-time"></i>
                        <span
                            style="margin-left: 10px;"
                        >{{dataToNormalFormat(scope.row.taskBeginTime)}}</span>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot:default="scope">
                        <i class="el-icon-time"></i>
                        <span
                            style="margin-left: 10px;"
                        >{{dataToNormalFormat(scope.row.taskEndTime)}}</span>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="" :resizable="false">
                    <template v-slot="scope">
                        <el-button
                            @click="downloadById(scope.row.taskId)"
                            icon="el-icon-download"
                            type="primary"
                        ></el-button>
                        <!--  -->
                        <el-upload
                            class="upload"
                            :headers="header"
                            :action="uploadURL"
                            :data="{taskId: scope.row.taskId}"
                            :before-upload="beforeUpload"
                            :on-success="reload"
                        >
                            <el-button icon="el-icon-upload2" type="info"></el-button>
                        </el-upload>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>

<script>
import fileDownload from "js-file-download";
export default {
    data() {
        return {
            // 
            tasklist: [],
            // token
            header: {
                token: window.sessionStorage.getItem("token"),
            },
            // 
            uploadURL: "https://class2.youaremine.vip/file/upload",
        };
    },
    created() {
        this.getTasksList();
    },
    methods: {
        // 
        async getTasksList() {
            this.tasklist = [];
            let flag = false;
            const res = await this.$http.get("/user/userTasks");

            // if (res.data.code != 200)
            //     return this.$message.error("");

            console.log(res);
            //  
            for (let i = 0; i < res.data.data.doneTasks.length; ++i) {
                let params = new URLSearchParams();
                params.append("taskId", res.data.data.doneTasks[i]);
                const task = await this.$http.post(
                    "/user/taskIdToTask",
                    params
                );
                if (task.data.code != 200) flag = true;
                this.tasklist.push(task.data.data);
            }
            if (flag) this.$message.error("");
        },
        // 
        dataToNormalFormat(data) {
            return (
                data.substr(0, 4) +
                "-" +
                data.substr(5, 2) +
                "-" +
                data.substr(8, 2)
            );
        },
        // 
        async downloadById(id) {
            let params = new URLSearchParams();
            params.append("taskId", id);
            const res = await this.$http.post("/file/download", params);
            const uuid = res.data.data;
            let a = document.createElement("a");
            a.href =
                `${this.$store.state.downloadURL}` +
                uuid;
            document.body.appendChild(a);
            a.click(); //
            URL.revokeObjectURL(a.href); // URL 
            document.body.removeChild(a); //  a 
        },
        // 
        reload() {
            this.getTasksList();
            this.$message.success('');
        },
        // 
        beforeUpload(file) {
            var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
            const isLt10M = file.size / 1024 / 1024 < 50; //
            if (!isLt10M) {
                this.$message.error(" 50MB!");
            }
            return isLt10M;
        },
    },
};
</script>

<style lang="less" scoped>
.el-table th.gutter {
    display: table-cell !important;
}
.infoTable_box {
    margin: auto;
    width: 650px;
    text-align: center;
}
.line {
    text-align: center;
}
.btns {
    display: flex;
    justify-content: flex-end;
}
.expandTitle {
    letter-spacing: 3px;
}
.upload {
    display: inline-block;
    margin-left: 10px;
}
</style>